import moment from 'moment';
import _ from 'lodash';

import SuperAgent from '../models/SuperAgent';
import Agent from '../models/Agent';
import Player from '../models/Player';
import AgentDeposit from '../models/AgentDeposit'
import { HistoryBet, OpenBet } from '../models/BetOrder';
import AgentTransaction from '../models/AgentTransaction';
import updatePlayerStatusAfterOrder from '../updateDB/updateUser/updatePlayerStatusAfterOrder'

import express from 'express';
const router = express.Router();


router.get('/make-agent-transaction', async (req, res) => {

	let preTransactions = []

	const agent = await Agent.findOne({ 'account.username': 'diamond' })
	const agentDeposits = await AgentDeposit.find({ 'owner.agent': agent._id })
	const agentHistoryBets = await HistoryBet.find({ 'owner.agent': agent._id, 'status': 'Lost' }, 'orderNumber orderType resultAmount owner')

	preTransactions = agentDeposits.concat(agentHistoryBets)

	preTransactions.sort((a, b) => { return new Date(a.closedAt) - new Date(b.closedAt) })

	let resultAmount = 0

	await Promise.all(preTransactions.map(async transaction => {
		let newAgentTransaction = new AgentTransaction({
			owner:{
				// player: { type: Schema.Types.ObjectId, ref: 'Player' },
				superAgent : agent.superAgent,
				agent : agent._id
			},
			orderType: transaction.orderType,
//			transactionType: { type: String, enum: [ 'in', 'out' ] },
//			creditAmount: { type: Number },
//			resultAmount: { type: Number },
			orderNumber: transaction.orderNumber,
			createdAt: moment()
		})

		if(transaction.orderType === 'Deposit'){
			resultAmount += transaction.creditAmount
			newAgentTransaction.transactionType = 'in'
			newAgentTransaction.creditAmount = transaction.creditAmount
			newAgentTransaction.resultAmount = resultAmount
		}else{
			resultAmount += transaction.resultAmount
			newAgentTransaction.transactionType = 'out'
			newAgentTransaction.owner.player = transaction.owner.player
			newAgentTransaction.creditAmount = transaction.resultAmount
			newAgentTransaction.resultAmount = resultAmount
		}

		await newAgentTransaction.save()

	})).then( async () => {

		const agentOpenBets = await OpenBet.find({ 'owner.agent': agent._id }, 'orderType wagerDetail.riskAmount wagerDetail.winAmount owner')
		const agentCreditPending = agentOpenBets.reduce((total, openBet) => total + openBet.wagerDetail.riskAmount, 0)
		const activePlayerCounter = _.uniqBy([].concat.apply([], agentOpenBets.map(openBet => openBet.owner.player)))

		const agentCurrentStatus = {
			activePlayer: activePlayerCounter.length,
			credit: resultAmount,
			creditPending: agentCreditPending,
			availableCredit: resultAmount - agentCreditPending
		}

		await Agent.findOneAndUpdate({ _id: agent._id }, {'$set': { currentStatus: agentCurrentStatus }})

	}).then(() => {
		res.json('done')
	})

	// res.json('done')

})

































router.get('/push', async (req, res) => {

	const agent = await Agent.findOne({ 'account.username': 'diamond' })
	const players = await Player.find({ agent: agent._id }, '_id')

	players.forEach(player => {
		agent.players.push(player)
	})

	await agent.save()
	console.log(players)
	res.json(agent)

})

router.get('/setup', async (req, res) => {
	const players = await Player.find({})
	await Promise.all(players.map(async player => {
		await updatePlayerStatusAfterOrder(player._id)
	})).then(() => {
		res.json('done')
	})
})


